          <div class="p1">
            挂牌三农服务站
            <span
              style="position:relative; top:-3%;left:5%;color:#FAFC0F;font-size:40px;"
            >3456</span>
            <span class="p1_2">人</span>
          </div>
          <div class="p2">
            下辖人员
            <span class="p2_1">456</span>人
          </div>
          <div class="p2">
            商务
            <span class="p2_1">234</span>人
          </div>
          <div class="p2">
