        <div id="p_bottom" class="background-image">
          <div style="margin-top:70px;margin-left:100px;font-size:24px;">
            标准站点覆盖面
            <span
              style="position:absolute; margin-top:-13px;margin-left:15px;color:#13C7EC;font-size:40px;"
            >85%</span>
          </div>
        </div>
      </div>
      <div class="legend"></div>
    </div>
    
    <!-- <div class="myBlog">
      <a href="https://dongkelun.com" target="_blank" title="伦少的博客">
        <img src="https://dongkelun.com/img/favicon.ico" alt="伦少的博客" />
