      </a>
    </div>-->
  </div>
</template>

<script>
import cityMap from "@/js/china-main-city-map.js";
import echarts from "echarts";
import axios from "axios";
import Vue from "vue";

import { getMapData } from "@/api/dashboard";

// import registerAndsetOption from '@/js/echarts-map'

//中国地图（第一级地图）的ID、Name、Json数据
var provinceId = 320000;
var provinceName = "江苏省";
var chinaJson = null;

//记录父级ID、Name
var mapStack = [];
var parentId = null;
var parentName = null;

//Echarts地图全局变量，主要是在返回上级地图的方法中会用到
var myChart = null;
export default {
  name: "chinaMap",
  props: {
    msg: String,
  },
  data() {
    return {
      p_name: "江苏省",
    };
  },
  mounted: function () {
    this.mapChart("mapChart");
  },
  methods: {
    /**
     * 返回上一级地图,这里其实等于固定返回江苏地图一级
     */
    back() {
      if (mapStack.length != 0) {
        //如果有上级目录则执行
        var map = mapStack.pop();
        const that = this;
        getMapData(map.mapName).then((response) => {
          const data = response.data;
          province_color_data = [];
          province_data = [];
          const that = this;
          that.p_name = map.mapName;
          if (data) {
            if (data.color) {
              province_color_data = data.color;
            }

            if (data.coord) {
              let coord = data.coord;
              let img_arr = data.img_arr;
              for (var i = 0; i < coord.length; i++) {
                province_data.push({
                  value: coord[i].split(","),

                  symbol: img_arr[i],
                });
              }
            }
          }

          axios
            .get("./static/json/map/" + map.mapId + ".json", {})
            .then((response) => {
              const mapJson = response.data;
              registerAndsetOption(
                myChart,
                map.mapId,
                map.mapName,
                mapJson,
                false,
                province_color_data,
                province_data
              );

              //返回上一级后，父级的ID、Name随之改变
              parentId = map.mapId;
              parentName = map.mapName;
            });
        });
      }
    },
    /**
     * Echarts地图
     */

    mapChart(divid) {
      //省级数据
      getMapData(provinceName).then((response) => {
        const data = response.data;
        console.log(data);
        province_color_data = data.color;

        let coord = data.coord;

        let img_arr = data.img_arr;
        province_data = [];
        for (var i = 0; i < coord.length; i++) {
          province_data.push({
            value: coord[i].split(","),

            symbol: img_arr[i],
          });
        }

        // for (var i = 0; i < province_color.province_name.length; i++) {
        //   province_color_data.push({
        //     name:  province_color.province_name[i],
        //     // .replace("省", "").replace("市", ""),
        //     itemStyle: {
        //       areaColor:  province_color.color[i]
        //     }
        //   });

        // }

        axios
          .get("./static/json/map/" + provinceId + ".json", {})
          .then((response) => {
            const mapJson = response.data;
            chinaJson = mapJson;
            myChart = echarts.init(document.getElementById(divid));
            registerAndsetOption(
              myChart,
              provinceId,
              provinceName,
              mapJson,
              false,
              province_color_data,
              province_data
            );
            parentId = provinceId;
            parentName = provinceName;
            const that = this;

            myChart.on("click", function (param) {
              console.log(that.p_name);
              that.p_name = param.name;
              var cityId = cityMap[param.name];
              if (cityId) {
                //代表有下级地图

                getMapData(param.name).then((response) => {
                  const data = response.data;
                  province_color_data = [];
                  province_data = [];
                  console.log(data);
                  if (data) {
                    if (data.color) {
                      province_color_data = data.color;
                    }

                    if (data.coord) {
                      let coord = data.coord;
                      let img_arr = data.img_arr;
                      for (var i = 0; i < coord.length; i++) {
                        province_data.push({
                          value: coord[i].split(","),

                          symbol: img_arr[i],
                        });
                      }
                    }
                  }

                  axios
                    .get("./static/json/map/" + cityId + ".json", {})
                    .then((response) => {
                      const mapJson = response.data;
                      registerAndsetOption(
                        myChart,
                        cityId,
                        param.name,
                        mapJson,
                        true,
                        province_color_data,
                        province_data
                      );
                    });
                });
              } else {
                //没有下级地图，只修改右侧内容
                mapStack.push({
                  mapId: parentId,
                  mapName: parentName,
                });
                // parentId = id;
                // parentName = name;

                //没有下级地图，回到一级中国地图，并将mapStack清空
                // registerAndsetOption(
                //   myChart,
                //   provinceId,
                //   provinceName,
                //   chinaJson,
                //   false,
                //   [],
                //   []
                // );
                // mapStack = [];
                // parentId = provinceId;
                // parentName = provinceName;
              }
              // $.get('./asset/json/map/'+param.data.id+'.json', function (mapJson,status) {

              //     registerAndsetOption(myChart,param.data.id,param.name,mapJson,true)

              // }).fail(function () {
              //     registerAndsetOption(myChart,provinceId,'china',chinaJson,false)
              // });
            });
          });
      });
    },
  },
};
/**
 *
 * @param {*} myChart
 * @param {*} id        省市县Id
 * @param {*} name      省市县名称
 * @param {*} mapJson   地图Json数据
 * @param {*} flag      是否往mapStack里添加parentId，parentName
 */
function registerAndsetOption(myChart, id, name, mapJson, flag, data1, data3) {
  console.log(data1);
  echarts.registerMap(name, mapJson);
  myChart.setOption({
    geo: {
      map: name,
      roam: false,
      itemStyle: {
        normal: {
          areaColor: "rgba(23, 27, 57,0)",
          borderColor: "#21B5F9",
          borderWidth: 1,
        },
      },
      layoutSize: "95%",
      layoutCenter: ["50%", "50%"],
    },
    series: [
      {
        type: "map",
        map: name,
        data: data1,
        itemStyle: {
          normal: {
            show: true,
            areaColor: "rgba(23, 27, 57,0)",
            borderColor: "#21B5F9",
            borderWidth: 1,
            color: "#fad715",
          },
        },
        layoutSize: "95%",
        layoutCenter: ["50%", "50%"],
        label: {
          normal: {
            show: true,
            color: "#E9FFFF",
            fontSize: 18,
          },
          // emphasis: {
          //   show: false
          // }
        },

        data: initMapData(mapJson, data1),
      },
      {
        type: "scatter",
        coordinateSystem: "geo",
        // color: '#fad715',
        symbolSize: 25,
        // zlevel: 0,
        // label: {
        // 	normal: {
        // 		show: true,
        // 		position: "bottom",
        // 		textStyle: {
        // 			color: "#fad715",
        // 			fontWeight: 'bold',
        // 			fontSize: 12
        // 		},
        // 		padding: 12,
        // 		align: 'left',
        // 		formatter: "{b}",
        // 		offset: [-60, 0]
        // 	},
        // },
        data: data3,
      },
    ],
  });

  if (flag) {
    //往mapStack里添加parentId，parentName,返回上一级使用
    mapStack.push({
      mapId: parentId,
      mapName: parentName,
    });
    parentId = id;
    parentName = name;
  }
}

function initMapData(mapJson, color) {
  console.log(color);
  var mapData = [];
  for (var i = 0; i < mapJson.features.length; i++) {
    mapData.push({
      name: mapJson.features[i].properties.name,
      itemStyle: {
        areaColor: color[mapJson.features[i].properties.name],
      },
      //id:mapJson.features[i].id
    });
  }
  return mapData;
}
function randomIntData(lowerLimit, range) {
  return Number((lowerLimit + Math.random() * range).toFixed(0));
}

var mapData1 = [];
var mapData2 = [];
var mapData3 = [];

var province_data = [];
var province_color_data = [];

var color = ["#FEC55E", "#FD7255", "#05DCC6", "#F76882", "#9A6EF5", "#088EFE"];
// for (var i = 0; i < province_name.length; i++) {
//   mapData1.push({
//     name: province_name[i].replace("省", "").replace("市", ""),
//     itemStyle: {
//       areaColor: color[i]
//     }
//   });
//   mapData3.push({
//     name: province_name[i].replace("省", "").replace("市", ""),
//     value: coord[i].split(","),

//     symbol: img_arr[i]
//   });
// }

function randomData(lowerLimit, range) {
  return Number((lowerLimit + Math.random() * range).toFixed(2));
}
</script>

<style>
body {
  padding-bottom: 0 !important;
  width: 100%;
  height: 100%;
  background-size: 100% 100%;
  	overflow: hidden;
}
</style>

