    linear-gradient(to left, #467dc3, #467dc3) right top no-repeat,
    linear-gradient(to bottom, #467dc3, #467dc3) right top no-repeat,
    linear-gradient(to left, #467dc3, #467dc3) left bottom no-repeat,
    linear-gradient(to bottom, #467dc3, #467dc3) left bottom no-repeat,
    linear-gradient(to left, #467dc3, #467dc3) right bottom no-repeat,
    linear-gradient(to left, #467dc3, #467dc3) right bottom no-repeat;
