}
#p_lable {
  position: absolute;
  color: #0fb8da;
  left: 2%;
  top: 6%;
  font-size: 26px;
}

#p_left1 {
  position: absolute;
  width: 41%;
  height: 30%;
  border: 1px solid #1f3e7a;
  left: 2%;
  top: 12%;
}

#p_left2 {
  position: absolute;
  width: 41%;
  height: 30%;
  border: 1px solid #193473;
  top: 45%;
  left: 2%;
}

#p_right1 {
  position: absolute;
  width: 41%;
  height: 30%;
  border: 1px solid #193473;
  right: 10%;
  top: 12%;
}

#p_right2 {
  position: absolute;
  width: 41%;
  height: 30%;
  border: 1px solid #193473;
  right: 10%;
  top: 45%;
}

.p1 {
  position: relative;
  top: 10%;
  left: 10%;
  width: 100%;
  height: 20%;
  font-size: 24px;
}

.p1_2 {
  position: relative;
  left: 10%;
}

.p2 {
  position: relative;
  top: 20%;
  left: 20%;
  width: 100%;
  height: 20%;
  font-size: 20px;
}
.p2_1 {
  position: relative;
  left: 5%;
  padding-right: 12%;
}

#p_bottom {
  position: absolute;
  width: 88%;
  height: 18%;
  border: 1px solid #193473;
  bottom: 3%;
  left: 2%;
}

.backBtn {
  position: absolute;
  top: 0;
  background-color: #026cb7;
  border: 0;
  color: #fff;
  height: 27px;
  font-family: Microsoft Yahei;
  font-size: 1em;
  cursor: pointer;
}
.myBlog {
  position: absolute;
  top: 2px;
  right: 5%;
  display: block;
  border: 2px solid #262a53;
}
.myBlog a {
  text-decoration: none;
  display: inline-block;
  color: #fff;
  padding: 5px;
  font-size: 1em;
}

.myBlog a img {
  vertical-align: middle;
  height: 20px;
  width: 20px;
  border-radius: 50%;
  margin: -5px 5px auto auto;
}

.bottom {
  position: absolute;
  width: 100%;
  height: 5%;
  line-height: 100%;
  left: 0;
  bottom: 0%;
  text-align: center;
}

.wrapper {
    width: 1920px;
    height: 1080px;
    position: absolute;
    left: 0;
    top:0;
    /* background: url(/images/obu/bak.png) no-repeat 17px 0px; */
    background-size:calc(100% - 1000px) 100%;
}
</style>



