import request from '@/utils/request'

export function upLeft(data) {
  return request({
    url: '/dashboard/upLeft',
    method: 'post',
    data
  })
}
export function left(data) {
  return request({
    url: '/dashboard/left',
    method: 'post',
    data
  })
}
export function upright(data) {
  return request({
    url: '/dashboard/upright',
    method: 'post',
    data
  })
}
export function right(data) {
  return request({
    url: '/dashboard/right',
    method: 'post',
    data
  })
}
export function lowleft(data) {
  return request({
    url: '/dashboard/lowleft',
    method: 'post',
    data
  })
}
export function getTotalMoney(data) {
  return request({
    url: '/dashboard/getTotalMoney',
    method: 'post',
    data
  })
}
export function getMapData(data) {
  console.log(data)
  return request({
    url: '/dashboard/getMapData',
    method: 'post',
    data
  })
}



